import React from 'react';
import { BrowserRouter as Router, Route, Routes, Link, useNavigate } from 'react-router-dom';
import { Element, scroller } from 'react-scroll';
import './App.css';
import ProductList from './components/ProductList';
import ContactUs from './components/ContactUs';
import aboutImage from './images/about-us.jpg';
import CartPage from './pages/CartPage';
import NotFoundPage from './pages/NotFoundPage'; // Import NotFoundPage
import { CartProvider, useCart } from './CartContext';

function App() {
  return (
    <CartProvider>
      <Router>
        <div className="App">
          <Header />
          <main>
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/cart" element={<CartPage />} />
              <Route path="*" element={<NotFoundPage />} /> {/* Fallback route */}
            </Routes>
          </main>
        </div>
      </Router>
    </CartProvider>
  );
}

function Header() {
  const navigate = useNavigate();
  const { getCartCount } = useCart();

  const handleScrollToSection = (sectionName) => {
    scroller.scrollTo(sectionName, {
      smooth: true,
      duration: 600,
      offset: -120,
    });
  };

  const handleNavigation = (path, sectionName) => {
    navigate(path, { replace: true });

    setTimeout(() => {
      handleScrollToSection(sectionName);
    }, 200);
  };

  return (
    <header className="App-header">
      <div className="header-content">
        <div className="navbar">
          <Link to="/" onClick={() => handleNavigation("/", "home")}>
            
          </Link>
          <Link to="/" onClick={() => handleNavigation("/", "products")}>
            
          </Link>
          <Link to="/" onClick={() => handleNavigation("/", "about")}>
             
          </Link>
          <Link to="/" onClick={() => handleNavigation("/", "contact")}>
             
          </Link>
          <Link to="/cart" className="cart-link">
            
            <span className="cart-count">{getCartCount()}</span>
          </Link>
        </div>
        <h1 className="header-title"> </h1>
      </div>
    </header>
  );
}

function Home() {
  return (
    <>
      <Element name="home">
        <div className="home-section">
          <h1>   </h1>
          <p>
                      .
          </p>
          <ProductList />
        </div>
      </Element>
      <Element name="about">
        <div className="AboutUs">
          <h2> </h2>
          <img src={aboutImage} alt="About Us" className="about-image" />
          <p>
            :   ,    ,    
            ,         .   
               ,     
             .        
            ,        .     
                        
            .          
            ,       .
          </p>
        </div>
      </Element>
      <Element name="contact">
        <ContactUs />
      </Element>
    </>
  );
}

export default App;
